import React, { Component } from 'react';
import { inject, observer } from 'mobx-react';
import { observable, action, useStrict } from "mobx";
import { Route, Switch, Link } from 'react-router-dom';
import User from './user';


/*
Mobx @ 
 babel-plugin-transform-decorators-legacy
package.jsonwebpack.config.jsplugins
*/
// mobx@action
useStrict(true);
class appState {
    @observable data = [1, 2, 3];
}

// stores
@inject('store')
// observerui
@observer
class App extends Component {

    @action handle() {
        this.props.appState.prototype.data.push(1);
        // stores
        this.props.store.fetchData.name = "Hello Mobx!";
    }

    render() {
        return (
            <div className="App" >
                <p className="App-intro" onClick={this.handle.bind(this)}>
                    {this.props.store.fetchData.name}
                </p>
                <Link to="/app/user"></Link>
                --
                <Link to="/home"></Link>

                <ul>
                    {this.props.appState.prototype.data.map((x, i) => {
                        return (
                            <li key={i}>
                                {i}
                            </li>
                        )
                    })}
                </ul>

            </div>
        );
    }
}

class AppS extends Component {

    render() {
        return (
            <div>
                <App appState={appState} />
                <div>
                    {/* userhome */}
                    {/* AppsAppurlstate props */}
                    <Switch>
                        <Route exact path="/app/user" component={User} />
                    </Switch>
                </div>
            </div>
        )
    }
}

export default AppS;



